  --black: #000;
  --grey: #b8b8b8;
  --white: #fff;
  --dark-grey: #4e4e4e;
}

html {
  scroll-behavior: smooth;
}

body {
  font-family: "DM Sans", "Segoe UI", Tahoma, Geneva, Verdana, sans-serif;
}

.prm-btn {
  background-color: transparent;
  border: none;
  outline: none;
  color: var(--white);
  letter-spacing: 2px;
  font-weight: 500;
  font-size: 1rem;
}

.prm-btn > svg {
  margin-left: 2rem;
  transform: translateY(4px);
  transition: transform 200ms ease;
}

.prm-btn:hover,
.prm-btn:focus {
  text-decoration: underline;
}

.prm-btn:hover > svg,
.prm-btn:focus > svg {
  transform: translate(-1.5rem, 4px);
}

#focus-establish {
  position: absolute;
  top: -1000px;
  left: -1000px;
}

